//importer bootstrap-icons bibliotek fr att kunna anvnda ikonerna fr support (telefon) och logga in (person)//
import 'bootstrap-icons/font/bootstrap-icons.css';
import './App.css';
import {HotelCard} from "./hotelcard";
import { NavBar } from "./navbar";
import { PinkHero } from "./pinkhero";
import {Footer} from "./footer";
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import BokaResa from "./bokaresa"; 
import Payment from "./payment";
import Confirmation from "./confirmation";
import ReadMore from "./readmore";

function App() {
  return (
    <Router>
    <div className="App">
      <NavBar />
      <Routes>
          <Route 
            path="/" 
            element={
              <div>
                <PinkHero />
                <HotelCard />
              </div>
            } 
          />
          <Route path="/bokaresa" element={<BokaResa />} /> {/* Tar anvndaren till Boka-vyn */}
          <Route path="/readmore" element={<ReadMore />} /> {/* Tar anvndaren till Ls mer-vyn */}
          <Route path="/payment" element={<Payment />}    /> {/* Tar anvndaren till Betalnings-vyn */}
          <Route path ="/confirmation" element={<Confirmation />}/> {/* Tar anvndaren till den sista vyn som r betalningsbekrftelsen */}
        </Routes>
        <Footer />
    </div>
     </Router>

     
  );
}

export default App;
